import { useState, useRef } from 'react';
import audioFile from '../music/1__.mp3'; // Adjust the path if needed

import SVG_6 from '../assets/svg/6__.svg'
import SVG_4 from '../assets/svg/4__.svg'
import SVG_7 from '../assets/svg/7__.svg'
import SHIFT from '../assets/svg/SHIFT.svg'


function UX() {
  const [isPlaying, setIsPlaying] = useState(false);
  const audio = useRef(null); // Use ref to store the audio element

  const togglePlay = () => {
    if (!audio.current) {
      audio.current = new Audio(audioFile); // Initialize the audio element on first play attempt
    }

    if (isPlaying) {
      audio.current.pause(); // Stop the music
    } else {
      audio.current.play();  // Start playing the music
    }
    setIsPlaying(!isPlaying); // Toggle the play/pause state
  };

  return (<>
{/* <div className="w-[100%] h-[100vh] fixed z-[0] top-0 right-0 shadow-[inset_0_40px_40px_rgba(0,0,0,0.8),inset_0_-40px_40px_rgba(0,0,0,0.8)] pointer-events-none">
</div> */}


<div className='fixed top-0 left-0 p-[20px] rounded w-full bg-opacity-50  text-[20px] flex gap-[20px] items-center justify-between bg-black'>
     
      <a href="/" className='font-bold text-3xl  rounded-full text-white'>
      <img className='h-full object-cover' src={SVG_6} alt='' />
      </a>
      <img src={SHIFT} alt="" srcset="" />
     
<button className=''>
      <img className='h-full object-cover' src={SVG_7} alt='' />
  </button>
</div>
<div className='gap-[32px] flex flex-col  fixed bottom-0 right-0  z-[9999] p-[16px] items-center'>



<button className="" onClick={togglePlay}>
<img
className={`h-full object-cover transition-transform duration-1000 ${isPlaying ? "animate-spin" : ""}`}
src={SVG_4}
alt=""
/>
</button>

</div>
    
  </>
  );
}

export default UX;
