import React, { useEffect, useState, useRef } from 'react';
import LOADING_SVG from '../assets/svg/loading.svg'
import SVG_1 from '../assets/svg/1__.svg'
import SVG_2 from '../assets/svg/2__.svg'
import SVG_3 from '../assets/svg/3__.svg'
import SVG_4 from '../assets/svg/4__.svg'
import LIKED from '../assets/svg/LIKED_SVG.svg'

function Main() {


  const debug = false;


  const MAIN_URL = debug ? 'https://nobox.kg/api_v1/posts/' : '/api_v1/posts/';
  const [posts, setPosts] = useState([]);
  const [visiblePosts, setVisiblePosts] = useState(4);  // Initially load 4 posts
  const [loading, setLoading] = useState(false);
  const loadMoreRef = useRef(null);
  const [liked, setLiked] = useState(false);

  useEffect(() => {
    fetchPosts();
  }, []);

  useEffect(() => {
    // Scroll to the element after the component is fully rendered
    const element = document.getElementById('toScroll');
    if (element) {
      setTimeout(() => {
        window.scrollTo(0, element.offsetTop);
      }, 100); // Wait for 100ms to ensure the component is fully rendered
    }
  }, [posts]); // Trigger after posts are set

  const fetchPosts = () => {
    setLoading(true);
    
    // Delay the loading message for 3 seconds
    setTimeout(() => {
      if (loading) {
        setLoading(true);
      }
    }, 3000); // 3-second delay

    fetch(MAIN_URL)
      .then(response => response.json())
      .then(data => {
        setPosts(data);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error fetching posts:', error);
        setLoading(false);
      });
  };

  const convertToHTTPS = (url) => {
    if (debug) {
      // console.log('Debug');
      return url;
    }

    if (url && url.startsWith('http://')) {
      return url.replace('http://', 'https://');
    }
    return url;
  };

  const handleIntersection = (entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting && !loading && visiblePosts < posts.length) {
        setVisiblePosts(prev => prev + 4);
        setLoading(true);
  
        setTimeout(() => {
          setLoading(false);
        }, 1000);  // 3-second delay
      }
    });
  };

  useEffect(() => {
    const likedItems = JSON.parse(localStorage.getItem("likedItems")) || [];
    setLiked(likedItems);
  }, []);

  const toggleLike = (postId) => {
    setLiked((prevLiked) => {
      // Ensure postId is a string
      const stringPostId = String(postId);

      let updatedLikedItems;
      if (prevLiked.includes(stringPostId)) {
        updatedLikedItems = prevLiked.filter((item) => item !== stringPostId);
      } else {
        updatedLikedItems = [...prevLiked, stringPostId];
      }

      // Store the updated list as strings in localStorage
      localStorage.setItem("likedItems", JSON.stringify(updatedLikedItems));
      return updatedLikedItems;
    });
  };

  useEffect(() => {
    const observer = new IntersectionObserver(handleIntersection, {
      rootMargin: '0px',
      threshold: 1.0, // The element must be fully visible
    });

    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current);
    }

    return () => {
      if (loadMoreRef.current) {
        observer.unobserve(loadMoreRef.current);
      }
    };
  }, [visiblePosts, posts.length, loading]);

  return (
    <>
      {/* <div className='h-[200px]'></div> */}
      <div className='w-[100%] h-[100%] text-white bg-white max-h-[100vh] flex items-center justify-center relative overflow-hidden'>
        <div className='w-[100%] max-w-[500px] bg-white max-h-[100vh] h-full snap-y snap-mandatory gap-[10px] overflow-y-scroll scrollbar-hidden'>
          {posts.slice(0, visiblePosts).map((post, index) => (
            <>
              <div key={index} className='h-[100vh]  text-black snap-center bg-black my-[10px] relative'>
                <div className='gap-[32px] flex flex-col absolute bottom-0 right-0 z-[10000] p-[16px] items-center'>
                  <button onClick={() => toggleLike(post.id)}>
                    <img
                      className="h-full object-cover"
                      src={liked.includes(String(post.id)) ? LIKED : SVG_1}
                      alt=""
                      style={{ color: "black" }}
                    />
                  </button>
                  <button>
                    <img className='h-full object-cover' src={SVG_2} alt='' />
                  </button>
                  <button className=' mb-[100px]'>
                    <img className='h-full object-cover' src={SVG_3} alt='' />
                  </button>
                </div>
                <div className="h-full flex justify-center items-center overflow-hidden relative">
                  <div className='absolute w-full bottom-[0px] px-[16px] pb-[50px] pt-[16px] text-white flex flex-wrap flex-col gap-2 z-[9999] bg-black bg-opacity-50'>
                    <h2 className="font-extrabold text-[40px]">
                      {post.price !== "0.00" ?
                        new Intl.NumberFormat("en-US", {
                          minimumFractionDigits: 0,
                          maximumFractionDigits: 0,
                        }).format(parseInt(post.price)) + ' C' : 'Договор'}{" "}
                    </h2>
                    <div className='flex flex-wrap gap-2'>
                      {post.tags.map((tag, tagIndex) => (
                        <h3 className="font-bold text-[20px]" key={tagIndex}>{tag}</h3>
                      ))}
                    </div>
                    <pre className=' text-wrap text-[16px] font-light '>{post.description.slice(0, 50) + (post.description.length > 50 ? "..." : "")}</pre>
                  </div>
                  <img className='h-full object-cover' src={convertToHTTPS(post.picture)} alt={`image-${index}`} />
                </div>
              </div>
              <div ref={loadMoreRef} className="h-1"></div>  {/* Invisible element to trigger intersection */}
            </>
          ))}
          {loading && (
            <div className="text-center text-white fixed left-[50%] top-[50%] z-[9999] translate-x-[-50%] translate-y-[-50%]">
              <img
                src={LOADING_SVG}
                alt=""
                className="animate-spin"
              />
              Загрузка...
            </div>
          )}
        </div>
      </div>
      <div id='toScroll'></div>
    </>
  );
}

export default Main;
